<template>
  <div>
    <Search></Search>
    <div class="commonNav">
      <div class="wrapper">
        <div class="part">
          <a href="/">
            <span>首页</span>
            <i></i>
          </a>
          <router-link to="/list">
            <span>找项目</span>
            <img class="hot" src="../../assets/hot.png">
            <i></i>
          </router-link>
          <router-link to="/rankingList">
            <span>排行榜</span>
            <i></i>
          </router-link>
          <router-link class="active" to="/news">
            <span>行业资讯</span>
            <i></i>
          </router-link>
        </div>
        <router-link class="issueBtn" to="/enter">发布招商项目<i class="el-icon-arrow-right"></i></router-link>
      </div>
    </div>
    <div class="greyBg">
      <div class="wrapper clearfix">
        <div class="onePart clearfix">
          <div class="newsAd clearfix">
            <div class="carousel fl">
              <el-carousel height="422px">
                <el-carousel-item v-for="item in adve[0]" :key="'adve0'+item.id">
                    <router-link class="small" :to="item.link" v-if="item.type != 1">
                      <img :src="item.img">
                      <span class="desc over">{{item.title}}</span>
                    </router-link>
                    <a :href="item.link" v-else target="_blank" class="small">
                      <img :src="item.img">
                      <span class="desc over">{{item.title}}</span>
                    </a>
                </el-carousel-item>
              </el-carousel>
            </div>
            <ul class="fr newsBig">
              <li v-for="item in adve[1]" :key="'adve1'+item.id">
                <router-link class="small" :to="item.link" v-if="item.type != 1">
                  <img :src="item.img">
                  <span >{{item.title}}</span>
                </router-link>
                <a :href="item.link" v-else target="_blank" class="small">
                  <img :src="item.img">
                  <span >{{item.title}}</span>
                </a>
              </li>
            </ul>
          </div>
          <div class="fixedWidth" style="height:422px;">
            <div style="height:382px;overflow:hidden;">
            <HotConsult currentMenu="8"></HotConsult>
            </div>
          </div>
        </div>

        <div class="twoPart clearfix">
          <div class="joinNews">
            <div class="title between">
              <h2>加盟资讯</h2>
              <router-link to="/newsList?type=2">
                <span>更多 &gt;</span>
              </router-link>
            </div>
            <div class="between">
              <div>
                <router-link :to="'/consultDetail?id='+item.id" v-for="item in join.info" :key="'info'+item.id">
                  <dl class="joinNewsDL">
                    <dt class="over">{{item.title}}</dt>
                    <dd class="clearfix">
                      <img :src="item.img">
                      <p class="desc">
                        <span>{{item.sign}}</span>
                        <i>{{item.addtime}}</i>
                      </p>
                    </dd>
                  </dl>
                </router-link>
              </div>
              <ul class="joinNewsList">
                <router-link :to="'/consultDetail?id='+item.id" v-for="item in join.stra" :key="'stra'+item.id">
                  <li class="clearfix" >
                    <span class="over"><b>・</b>{{item.title}}</span>
                    <i>{{item.addtime}}</i>
                  </li>
                </router-link>
              </ul>
            </div>
          </div>
          <div class="fixedWidth" style="height:480px;">
            <div>
              <Strategy></Strategy>
            </div>
          </div>
        </div>
        <div class="otherAd wrapper">
           <span v-for="item in adve[2]" :key="'adve2'+item.id">
              <router-link :to="item.link" v-if="item.type != 1"><img :src="item.img"></router-link>
              <a :href="item.link" v-else target="_blank"><img :src="item.img"></a>
          </span>
        </div>
        <div class="threePart clearfix">
          <div class="reportNews">
            <div class="title between">
              <h2>行业报告</h2>
              <router-link to="/newsList?type=0">
                <span>更多 &gt;</span>
              </router-link>
            </div>
            <dl class="reportNewsDL clearfix">
              <dt>
                <router-link :to="'/consultDetail?id='+industry.img[0].id">
                  <img :src="industry.img[0].img">
                  <span class="over">{{industry.img[0].title}}</span>
                </router-link>
              </dt>
              <dd>
                <ul class="joinNewsList">
                  <router-link :to="'/consultDetail?id='+item.id" v-for="item in industry.list" :key="'list'+item.id">
                  <li class="clearfix" >
                      <span class="over"><b>・</b>{{item.title}}</span>
                      <i>{{item.addtime}}</i>
                  </li>
                  </router-link>
                </ul>
              </dd>
            </dl>
          </div>
          <div class="fixedWidth" style="height:400px;">
            <div style="height:380px; overflow: hidden;">
              <hotOrder></hotOrder>
            </div>
          </div>
        </div>
        <div class="otherAd wrapper">
            <span v-for="item in adve[3]" :key="'adve3'+item.id">
              <router-link :to="item.link" v-if="item.type != 1"><img :src="item.img"></router-link>
              <a :href="item.link" v-else target="_blank"><img :src="item.img"></a>
          </span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import Search from '@/components/Search'
  import HotOrder from '@/components/HotOrder'
  import HotConsult from '@/components/HotConsult'
  import Strategy from '@/components/Strategy'
  import commonApi from '@/api/common-api'
  import api from '@/api/article-api'

  export default {
    name: "index.vue",
    data(){
      return{
        data:[],
        join:[],
        industry: {img:[{id:''}]},
        adve: [],
      }
    },
    mounted:function (){
      this.getData();
      this.getAdve();
    },
    methods:{
        getData: async function(){
            this.join = await api.getJoin();
            this.industry = await  api.getIndustry();
        },
        getAdve: async function(){
          this.adve = await commonApi.getAdve({wp:2})
        }
    },
    components: {
      Search,
      HotOrder,
      HotConsult,
      Strategy
    }
  }
</script>

<style lang="less" scoped>
  .commonNav{
    width:100%;
    height: 40px;
    line-height: 40px;
    background: #2F333B;
    font-size: 16px;
    color: #fff;
    .all{
      float: left;
      width: 240px;
      height: 40px;
      text-align: center;
      background: #51575D;
      .icon-mulu{
        margin-right: 12px;
      }
      img,label{
        display: inline-block;
        vertical-align: middle;
      }
      img{
        margin:-2px 14px 0 0;
      }
    }
    .part{
      a{
        float: left;
        width: 160px;
        height: 40px;
        position: relative;
        color: #fff;
        text-align: center;
        .hot{
          position: absolute;
          top: 4px;
          margin-left: 6px;
        }
      }
      a.active{
        background: #1A1D21;
        i{
          width: 34px;
          height: 2px;
          background: -webkit-gradient(linear, 100% 0, 0 0, from(#579EFA), to(#4F74F4));
          background: -webkit-linear-gradient(to bottom, #579EFA, #4F74F4);
          background: -moz-linear-gradient(to bottom, #579EFA, #4F74F4);
          background: -o-linear-gradient(to bottom, #579EFA, #4F74F4);
          background: linear-gradient(to bottom, #579EFA, #4F74F4);
          position: absolute;
          left: 50%;
          margin-left: -17px;
          bottom: 2px;
          z-index: 1;
        }
      }
    }
    .issueBtn{
      float: right;
      color: #FEAB17;
    }
  }
  .greyBg{
    padding: 10px 0 100px;
    background: #F4F5FA;
    .onePart .newsAd{
      float: left;
      width: 895px;
    }
    .twoPart .joinNews,.threePart .reportNews{
      float: left;
      width: 820px;
    }
    .onePart{
      .carousel{
        width: 595px;
        height: 422px;
        img{
          display: block;
          width: 595px;
          height: 422px;
        }
        .desc{
          width: 94%;
          height: 46px;
          line-height: 46px;
          padding: 0 3%;
          background: rgba(0,0,0,0.4);
          color: #fff;
          position: absolute;
          left: 0;
          bottom: 38px;
          z-index: 10;
        }
      }
      .newsBig{
        width: 290px;
        height: 422px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        li{
          width: 290px;
          height: 206px;
          cursor: pointer;
          position: relative;

          img{
            display: block;
            width: 290px;
            height: 206px;
          }
          span{
            width: 90%;
            height: 46px;
            line-height: 46px;
            padding: 0 5%;
            background: rgba(0,0,0,0.4);
            color: #fff;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
            position: absolute;
            left: 0;
            bottom: 0;
            z-index: 10;
          }
        }
      }
    }
    .twoPart{
      margin-top: 20px;
      .joinNews{
        padding: 0 41px 0 34px;
        height: 480px;
        background: #fff;
        .title{
          height: 72px;
          line-height: 72px;
          border-bottom: 1px solid #E3E4E5;
          color: #999;
          margin-bottom: 15px;
          h2{
            font-size: 20px;
            color: #000;
          }
        }
        .joinNewsDL{
          width: 368px;
          padding: 15px 0 15px;
          border-bottom: 1px dashed #E3E4E5;
          dt{
            height: 24px;
            line-height: 24px;
            font-size: 18px;
            color: #333;
            margin-bottom:15px;
          }
          dd{
            img{
              float: left;
              width: 156px;
              height: 110px;
            }
            .desc{
              float: right;
              width: 195px;
              font-size: 12px;
              span{
                display: block;
                color: #666;
                line-height: 24px;
                overflow: hidden;
                text-overflow: ellipsis;
                display: -webkit-box;
                -webkit-line-clamp: 2;
                -webkit-box-orient: vertical;
                margin: 13px 0;
              }
              i{
                display: block;
                color: #999;
                font-style: normal;
              }
            }
          }
        }
        .joinNewsDL:last-child{
          border-bottom: none;
        }
        .joinNewsList{
          width: 400px;
          margin-top: 11px;
          li{
            color: #333;
            height: 32px;
            line-height: 32px;
            span{
              float: left;
              max-width: 300px;
              height: 32px;
              line-height: 32px;
              b{
                margin-right: 3px;
                font-size: 26px;
                border-radius: 50%;
                vertical-align: middle;
              }
            }
            i{
              float: right;
              color: #999;
              font-size: 12px;
              font-style: normal;
            }
          }
        }
      }
    }
    .threePart{
      margin-top: 20px;
      .reportNews{
        padding: 0 41px 0 34px;
        height: 400px;
        background: #fff;
        .title{
          height: 72px;
          line-height: 72px;
          border-bottom: 1px solid #E3E4E5;
          color: #999;
          margin-bottom: 15px;
          h2{
            font-size: 20px;
            color: #000;
          }
        }
        .reportNewsDL{
          padding-top: 30px;
          dt{
            float: left;
            width: 370px;
            height: 260px;
            position: relative;
            img{
              display: block;
              width: 370px;
              height: 260px;
            }
            span{
              width: 90%;
              padding: 0 5%;
              height: 40px;
              line-height: 40px;
              background: rgba(0,0,0,0.4);
              color: #fff;
              position: absolute;
              left: 0;
              bottom: 0;
              z-index: 10;
            }
          }
          dd{
            float: right;
            width: 400px;
            .joinNewsList{
              li{
                color: #333;
                height: 32px;
                line-height: 32px;
                span{
                  float: left;
                  max-width: 300px;
                  height: 32px;
                  line-height: 32px;
                  b{
                    margin-right: 3px;
                    font-size: 26px;
                    border-radius: 50%;
                    vertical-align: middle;
                  }
                }
                i{
                  float: right;
                  color: #999;
                  font-size: 12px;
                  font-style: normal;
                }
              }
            }
          }
        }
      }
    }
    .otherAd{
      margin: 20px auto 0;
      a,img{
        display: block;
        width: 100%;
        height: 80px;
      }
    }
  }
  .el-carousel__indicators--horizontal{
    left: auto!important;
    right: 0!important;
  }
  .fixedWidth{
    float: right;
    width: 295px;
    background: #fff;
    overflow: hidden;
  }
</style>



